
  .basket {
    padding-top: 55px;
    background-color: #161516;
  }
  
  .basket-header {
    display: flex;

    &__btn {
      background: transparent;
      border: none;
      cursor: pointer;
      
    }
  
    &__title {
      margin-left: 60px;
      color: white;
      font-size: 31px;
      font-weight: 700;
      text-transform: uppercase;
    }

    &__link {
      margin-left: auto;
    }
    
    &__goout {
      padding: 12px 75px 12px 75px;
      height: 42px;
      font-size: 14px;
      font-weight: 400; 
      line-height: 17px;
      color: #D58C51;
      background-color: transparent;
      border: 1px solid #D58C51;
      cursor: pointer;
    }
  }
  
  .basket-main{
    margin: 0 auto;
    max-width: 860px;
    display: flex;
    flex-direction: column;
    padding-top: 38px;
    padding-bottom: 70px;
    height: calc(100vh - 180px);
    overflow-x: auto;
    scrollbar-width: thin;
  
  }
  
  .basket-main::-webkit-scrollbar {
    width: 2px;
    background-color: darkgrey;
  }
  
  .basket-main::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.6);
  }

  .basket-footer {
    border-top: 1px solid #D58C51;
    ;
    padding-top: 20px;
    padding-bottom: 27px;
  }

  .footer-container {
    margin: 0 auto;
    max-width: 860px;
  }
  
 
  